* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: var(--color-dark);
  color: #e0e0f0;
  overflow-x: hidden;
}
::selection {
  background: rgba(0, 240, 255, 0.3);
  color: #fff;
}
.glow-cyan {
  text-shadow: 0 0 10px rgba(0, 240, 255, 0.5), 0 0 40px rgba(0, 240, 255, 0.2);
}
.glow-purple {
  text-shadow: 0 0 10px rgba(123, 47, 255, 0.5), 0 0 40px rgba(123, 47, 255, 0.2);
}
.glow-box-cyan {
  box-shadow: 0 0 15px rgba(0, 240, 255, 0.15), inset 0 0 15px rgba(0, 240, 255, 0.05);
}
.glow-box-purple {
  box-shadow: 0 0 15px rgba(123, 47, 255, 0.15), inset 0 0 15px rgba(123, 47, 255, 0.05);
}
@keyframes scanline {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100vh);
  }
}
.scanline::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(to right, transparent, rgba(0, 240, 255, 0.08), transparent);
  animation: scanline 8s linear infinite;
  pointer-events: none;
  z-index: 9999;
}
.grid-bg {
  background-image: linear-gradient(rgba(0, 240, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 240, 255, 0.03) 1px, transparent 1px);
  background-size: 60px 60px;
}
@keyframes pulse-glow {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}
.pulse-glow {
  animation: pulse-glow 3s ease-in-out infinite;
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-8px);
  }
}
.float {
  animation: float 4s ease-in-out infinite;
}
html {
  scroll-behavior: smooth;
}
